* {
    background-color: #eaeaea;
}

.main-text {
    color: #eaeaea;
    font-size: 10em;
    text-shadow: 6px 6px 12px rgba(0,0,0, .08), -6px -6px 12px rgb(255, 255, 255, 0.5);
}

#mainbox {
    animation: loop 8s infinite;
    -webkit-animation: loop 8s infinite;
}
@keyframes loop
{
  0%   {transform: rotate(0deg);}
100%   {transform: rotate(360deg);}
}

.button:active {
    box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1),
    inset 2px 2px 8px rgba(126, 138, 167, 0.4), inset -4px -4px 10px #ffffff;
}
.sb-btn:focus {
    box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1),
    inset 2px 2px 8px rgba(126, 138, 167, 0.4), inset -4px -4px 10px #ffffff;
}
.sb-btn {
    border: 0px;
    padding: 10px;
    border-radius: 5px;
    outline: none;
    background: #eaeaea;
    box-shadow: 6px 6px 12px rgba(0,0,0, .08), -6px -6px 12px rgb(255, 255, 255, 0.5);
}
.neumorphism {
    border: 0px;
    padding: 10px;
    border-radius: 10px;
    margin: 40px 0px;
    outline: none;
    background: #eaeaea;
    box-shadow: 6px 6px 12px rgba(0,0,0, .08), -6px -6px 12px rgb(255, 255, 255, 0.5);
}
.pokeball{
    height: 200px;
    width: 200px;  
    border-radius: 100%;
    padding: 10px;
    background: #eaeaea;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 6px 6px 12px rgba(0,0,0, .08), -6px -6px 12px rgb(255, 255, 255, 0.5);
    overflow: hidden;
}
.pokecenter {
    width: 28%;
    height: 50px;
    border-radius: 100%;
    padding: 10px;
    background: #eaeaea;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 99;
    box-shadow: inset 2px 2px 8px rgba(126, 138, 167, 0.4), inset -4px -4px 10px #ffffff;
}
.minicenter{
    width: 25px;
    border-radius: 100%;
    height: 25px;
    box-shadow: 6px 6px 12px rgba(0,0,0, .08), -6px -6px 12px rgb(255, 255, 255, 0.5);
}
.pokecenterleft{
    height: 25px;
    width: 110%;
    border-radius: 5px;
    position: absolute;
    left: -10px;
    box-shadow:  6px 6px 12px rgba(0,0,0, .08), -6px -6px 12px rgb(255, 255, 255, 0.5);
}

.pokeball-box {
    display: flex;
    align-items: center;
    justify-content: center;
}

@media screen and (max-width: 700px) {
    .main-text { 
        font-size: 5em;
    }

 }